.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

@font-face {
  font-family: "Roboto-Bold";
  src: url(../fonts/Roboto/Roboto-Bold.ttf) format("truetype"), url(../fonts/Roboto/Roboto-BoldItalic.ttf) format("truetype");
}

@font-face {
  font-family: "Roboto-Italic";
  src: url(../fonts/Roboto/Roboto-Italic.ttf) format("truetype");
}

@font-face {
  font-family: "Roboto-Light";
  src: url(../fonts/Roboto/Roboto-Light.ttf) format("truetype"), url(../fonts/Roboto/Roboto-LightItalic.ttf) format("truetype");
}

@font-face {
  font-family: "Roboto-Medium";
  src: url(../fonts/Roboto/Roboto-Medium.ttf) format("truetype"), url(../fonts/Roboto/Roboto-MediumItalic.ttf) format("truetype");
}

@font-face {
  font-family: "Roboto-Regular";
  src: url(../fonts/Roboto/Roboto-Regular.ttf) format("truetype");
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  color: #000000;
  background-color: #ffffff;
  font-family: "Roboto-Regular", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: inline-block;
  border: none;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  background-image: -moz-linear-gradient(90deg, #ffca28 0%, #fff176 100%);
  background-image: -webkit-linear-gradient(90deg, #ffca28 0%, #fff176 100%);
  background-image: -ms-linear-gradient(90deg, #ffca28 0%, #fff176 100%);
  box-shadow: 0px 4px 0px 1px rgba(216, 165, 8, 0.004);
  width: 178px;
  font-family: "Roboto-Bold", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  transition: all 0.2s;
  outline: none;
}

.btn:active {
  transform: scaleX(0.9) translateY(4px);
  outline: none;
}

.btn:not(:active) {
  box-shadow: 0px 4px 0px 1px #d8a508;
}

.icon-phone {
  font-size: 18px;
  border: 1px solid #000000;
  border-radius: 50px;
  padding: 3px;
  margin-right: 15px;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-image: url('../img/header__bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 400px;
}

.header__tel {
  display: inline-block;
  text-decoration: none;
  color: #000000;
  font-size: 22px;
  line-height: 34px;
  margin-bottom: 10px;
  font-family: "Roboto-Bold", sans-serif;
}

.header__h1 {
  margin-top: 70px;
  font-size: 36px;
  text-align: center;
  font-family: "Roboto-Bold", sans-serif;
  text-transform: uppercase;
  color: #0094d9;
}

.header__navbar {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
}

.header__navbar ul {
  list-style: none;
  margin: 0;
  font-family: "Roboto-Regular", sans-serif;
  font-size: 18px;
}

.header__navbar ul li {
  border-bottom: 1px solid #959595;
}

.header__navbar ul li:last-child {
  border-bottom: none;
}

.header__navbar ul li a {
  display: block;
  padding-bottom: 5px;
  padding-top: 5px;
  text-decoration: none;
  color: #000000;
}

.header__wrap {
  position: relative;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 20px;
}

.header__navbar-right {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.advantagesItems {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  padding-top: 70px;
}

.header__btn-center {
  padding-top: 85px;
  padding-bottom: 60px;
  text-align: center;
}

.advantagesItems__item {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 250px;
}

.advantagesItems__item img {
  align-self: center;
}

.advantagesItems__item span {
  display: inline-block;
  font-size: 16px;
}

.advantagesItems__item .title {
  padding-top: 25px;
  text-transform: uppercase;
  font-weight: bold;
  font-family: "Roboto-Bold", sans-serif;
}

.header__btn-width {
  width: 361px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  width: 320px;
  height: 100%;
  background-color: #ffffff;
  z-index: 100000;
}

.mobile-menu ul {
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.mobile-menu ul li {
  padding: 20px;
  border-bottom: 1px solid #959595;
}

.mobile-menu ul li:active {
  background-color: #ffde52;
}

.mobile-menu ul li:last-child {
  border-bottom: none;
}

.mobile-menu ul a {
  display: block;
  padding-bottom: 5px;
  padding-top: 5px;
  text-decoration: none;
  color: #000000;
}

.button-close {
  margin-top: 5px;
  margin-left: 150px;
  width: 33px;
  height: 33px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.button-close::before,
.button-close::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 152px;
  width: 29px;
  height: 3px;
  background-color: #000000;
}

.button-close::before {
  transform: rotate(45deg);
}

.button-close::after {
  transform: rotate(-45deg);
}

.mobile-menu-opened {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
  height: 50px;
  z-index: 2;
  display: block;
  cursor: pointer;
  border: none;
}

.mobile-menu-opened::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 10px;
  width: 28px;
  height: 4px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main {
  margin-top: -15px;
  background-image: url('../img/main__bg.png');
  background-repeat: repeat-y;
  background-size: cover;
}

.main h2 {
  margin: 0;
  padding-top: 50px;
  text-align: center;
}

.main h4 {
  margin: 0;
}

.main p {
  margin: 0;
}

.slider-flex {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
}

.slider-block {
  padding-top: 40px;
}

.slider__wrap {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
  padding-top: 50px;
}

.slider__img {
  width: 330px;
}

.slider h4 {
  font-family: "Roboto-Bold", sans-serif;
}

.slider__text {
  width: 480px;
  padding-left: 25px;
  padding-top: 25px;
}

.slider__text h4 {
  margin-top: 60px;
}

.slider .price {
  font-size: 18px;
  font-family: "Roboto-Bold", sans-serif;
  padding-right: 10px;
}

.slider .subprice {
  display: inline-block;
  font-size: 36px;
  font-family: "Roboto-Bold", sans-serif;
  color: #0094d9;
  padding-right: 25px;
}

.slider__item {
  padding-top: 30px;
  padding-bottom: 75px;
}

.slider__item img {
  width: 154px;
}

.slick-active {
  outline: none;
}

.slick-active-current {
  outline: none;
}

.arrow {
  position: absolute;
  z-index: 9999999;
  border: none;
  background: transparent;
  font-size: 66px;
  top: 50%;
  transform: translateY(-50%);
}

.arrow:active {
  outline: none;
}

.arrow:hover {
  color: #0094d9;
}

.next {
  left: 87%;
}

.footer {
  background: -webkit-linear-gradient(90deg, #33a8f7 0%, #2579bf 100%);
  background: -o-linear-gradient(90deg, #33a8f7 0%, #2579bf 100%);
  background-image: -ms-linear-gradient(90deg, #33a8f7 0%, #2579bf 100%);
  background: linear-gradient(90deg, #33a8f7 0%, #2579bf 100%);
  padding-top: 50px;
  font-family: "Roboto-Light", sans-serif;
  font-size: 18px;
  line-height: 1.4;
}

.footer ul {
  margin: 0;
  width: 200px;
}

.footer ul li {
  list-style: none;
}

.footer ul a {
  display: block;
  text-decoration: none;
  color: #ffffff;
  padding-top: 10px;
}

.footer ul a:first-child {
  padding-top: 0;
}

.footer ul a:hover {
  color: #ffde52;
}

.footer__social {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.social-link {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
}

.social-link li a {
  padding-right: 10px;
  font-size: 14px;
}

.footer__items {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  padding-bottom: 45px;
}

.footer__nav {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
}

.footer__nav ul li:first-child {
  border-bottom: 1px solid #ffffff;
}

.footer__menu {
  padding-left: 0;
}

.footer__menu li {
  border-bottom: 1px solid #ffffff;
}

.footer__menu li:last-child {
  border-bottom: none;
}

