.header {
background-image: url('../img/header__bg.png');
background-repeat: no-repeat;
background-size: cover;
min-height: 400px;
  &__tel{
    display: inline-block;
    text-decoration: none;
    color: @black;
    font-size: 22px;
    line-height: 34px;
    margin-bottom: 10px;
    font-family: "Roboto-Bold", sans-serif;

  }
  &__h1{
    .sm-block({
      font-size: 30px;
      });
      .xs-block({
        font-size: 26px;
        });
    margin-top: 70px;
    font-size: 36px;
    text-align: center;
    font-family: "Roboto-Bold", sans-serif;
    text-transform: uppercase;
    color: @blue;
  }

}
.header__navbar{
  .md-block({
    display: none;
    });
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  ul{
    list-style: none;
    margin:0;
    font-family: "Roboto-Regular",  sans-serif;
    font-size: 18px;
      li{
        border-bottom: 1px solid @grey;
        &:last-child{
          border-bottom: none;
        }
      }
      & li a {
        display: block;
        padding-bottom:5px;
        padding-top: 5px;
        text-decoration: none;
        color: @black;
      }
  }
}
.header__wrap{
  position: relative;
  .sm-block({
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    });
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 20px;
}
.header__navbar-right{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  .header__tel {
    i{
      .sm-block({
        display: none;
        });
    }
  }
}
.advantagesItems{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  padding-top: 70px;
  .sm-block({
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    });
    .xs-block({
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: center;
      });
}
.header__btn-center{
  padding-top: 85px;
  padding-bottom: 60px;
  text-align: center;

}
.advantagesItems__item{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 250px;
    .sm-block({
      padding-bottom: 10px;
      });
      .xs-block({
        padding-bottom: 10px;
        });
    & img{
      align-self: center;
      .xs-block({
        padding-top: 10px;
        });
    }
    span{
      display: inline-block;
      font-size: 16px;
    }
     .title{
       padding-top: 25px;
      text-transform: uppercase;
      font-weight: bold;
      font-family: "Roboto-Bold", sans-serif;
    }
}
.header__btn-width{
  width:  361px;
  padding-top: 15px;
  padding-bottom: 15px;
  .sm-block({
    width:  250px;
    });
}
.mobile-menu{
  display: none;
  position: fixed;
  top:0;
  right: 0;
  width: 320px;
  height: 100%;
  background-color: @white;
  z-index: 100000;
  ul{
    text-align: center;
    list-style: none;
    margin: 0;
    padding: 0;
    li{
      padding: 20px;
      border-bottom: 1px solid @grey;
      &:active{
        background-color: @yellow;
      }
      &:last-child{
        border-bottom: none;
    }
  }
  a{
      display: block;
      padding-bottom:5px;
      padding-top: 5px;
      text-decoration: none;
      color: @black;
  }
}
}
.button-close{
    margin-top: 5px;
    margin-left: 150px;
    width: 33px;
    height: 33px;
    font-size: 0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    /////////////рисуем кнопку крестик////////////
    &::before, &::after{
      content: "";
      position: absolute;
      top: 20px;
      left: 152px;
      width: 29px;
      height: 3px;
      background-color: @black;
    }
    &::before{
      transform: rotate(45deg);
    }
    &::after{
      transform: rotate(-45deg);
    }
  }
  @media  screen and (
      max-width: 1600px
    ) {
   .mobile-menu-opened{display: none;}
    }

  .mobile-menu-opened{

    .lg-block({display: none;});
    .md-block({display: block;
      top: 30px;
    	right: 20px;
      width: 50px;
      height: 50px;});
    .sm-block({display: block;});
    .xs-block({display: block;});
  	position: absolute;
  	top: 0px;
  	right: 0px;
    width: 50px;
    height: 50px;
  	z-index: 2;
  	display: block;
  	cursor: pointer;
  	border: none;

  }
  .mobile-menu-opened::before {
  	content: "";
  	position: absolute;
  	top: 17px;
  	right: 10px;
  	width: 28px;
  	height: 4px;
  	background-color: @black;
  	box-shadow: 0 8px 0 0  @black, 0 16px 0 0  @black;
  }
