html{
  font-size: 16px;
}
body{
  margin: 0;
  padding: 0;
  color: @black;
  background-color: @white;
  font-family: "Roboto-Regular",  sans-serif;
  // overflow-x: hidden;
}
img{
  max-width: 100%;
  height: auto;
}
.btn{
  display: inline-block;
  border: none;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  background-image: -moz-linear-gradient( 90deg, rgb(255,202,40) 0%, rgb(255,241,118) 100%);
  background-image: -webkit-linear-gradient( 90deg, rgb(255,202,40) 0%, rgb(255,241,118) 100%);
  background-image: -ms-linear-gradient( 90deg, rgb(255,202,40) 0%, rgb(255,241,118) 100%);
  box-shadow: 0px 4px 0px 1px rgba(216, 165, 8, 0.004);
  width: 178px;
  font-family: "Roboto-Bold", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  transition: all 0.2s;
  outline: none;
  &:active{
  transform: scaleX(0.9) translateY(4px);
  outline: none;
}
&:not(:active){
  box-shadow: 0px 4px 0px 1px #d8a508 ;
}
}
.icon-phone{
  font-size: 18px;
  border: 1px solid @black;
  border-radius: 50px;
  padding: 3px;
  margin-right: 15px;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden{
	position: absolute;

	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;}
