.footer{
 background: -webkit-linear-gradient( 90deg, rgb(51,168,247) 0%, rgb(37,121,191) 100%);
 background: -o-linear-gradient( 90deg, rgb(51,168,247) 0%, rgb(37,121,191) 100%);
 background-image: -ms-linear-gradient( 90deg, rgb(51,168,247) 0%, rgb(37,121,191) 100%);
 background: linear-gradient(90deg,rgb(51,168,247) 0%, rgb(37,121,191) 100%);
 padding-top: 50px;
 	font-family: "Roboto-Light", sans-serif;
  font-size: 18px;
  line-height: 1.4;
 ul {
   margin: 0;
   width: 200px;
   // &:last-child {
   //   width: 220px;
   // }
 & li {
   list-style: none;
   }
   & a {
     display: block;
     text-decoration: none;
     color: @white;
      padding-top: 10px;
      &:first-child{
          padding-top: 0;
      }
     &:hover{
       color: @yellow;
     }
   }
 }
 &__items{
  .md-block({
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    });
 }
}
.footer__social{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .md-block({
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    });
    .sm-block({
      flex-direction: column;
      align-items: center;
      });

}
.social-link{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  & li a{
    padding-right: 10px;
    font-size: 14px;
  }
}
.footer__items{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  padding-bottom: 45px;
}
.footer__nav{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  .sm-block({
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    ul{
      padding-top: 20px;
      padding-bottom: 20px;
      padding-left: 0;
    }
    });
    & ul{

      & li{
      &:first-child {
        border-bottom: 1px solid @white;
      }
    }
}
}

.footer__menu{
  padding-left: 0;
  li {
    border-bottom: 1px solid @white;
    &:last-child{
      border-bottom: none;
    }
  }
}
.footer__logo{
  .sm-block({
    padding-bottom: 10px;
    });
}
