.main{
  margin-top:-15px;
  background-image: url('../img/main__bg.png');
  background-repeat: repeat-y;
  background-size: cover;
  h2{
    margin: 0;
    padding-top: 50px;
    text-align: center;
  }
  h4{
    margin: 0;
  }
  p{
    margin: 0;
  }
}
.slider{

  &-flex{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    .md-block({
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      });
    .sm-block({
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      });
  }
  &-block{
  padding-top: 40px;
}
  &__wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    padding-top: 50px;
    .sm-block({
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      });
      .xs-block({
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        });
  }
  &__img{
    width: 330px;
  }
  h4{
    font-family: "Roboto-Bold", sans-serif ;
  }
  &__text{
    width: 480px;
    padding-left:25px;
    padding-top:25px;
    h4{
      margin-top: 60px;
    }
  }
  .price{
    font-size: 18px;
    font-family: "Roboto-Bold", sans-serif ;
    padding-right: 10px;
  }
  .subprice{
    display: inline-block;
    font-size: 36px;
    font-family: "Roboto-Bold", sans-serif ;
    color: @blue;
    padding-right: 25px;
    .md-block({
      padding-right: 15px;
      padding-top: 10px;
      padding-bottom: 10px;
      });
      .sm-block({
        padding-right: 0;
        });
        .xs-block({
          padding-right: 0;
          });
  }
}

.slider__item{
  padding-top: 30px;
  padding-bottom: 75px;
  img{
      width: 154px;
  }
}
.slick-active{
  outline: none;
  &-current{
    outline: none;
  }
}

.btn__slider{
  .md-block({
      margin-bottom: 5px;
    });
  .sm-block({
      margin-bottom: 5px;
    });
}
.arrow{
  position: absolute;
  z-index:  9999999;
  border: none;
  background: transparent;
  font-size: 66px;
  top: 50%;
  transform: translateY(-50%);
  .sm-block({
    top: 53%;
    });
  &:active{
    outline: none;
  }
  &:hover{
    color: @blue;
  }
}
.prev{
  .sm-block({
    left: 20%;
    });
}
.next{
  left: 87%;
  .sm-block({
    left: 55%;
    });
}
